function validateInfo(user) {
  let errors = {};

  if (!user.Name.trim()) {
    errors.Name = "Username Required";
  }

  if (!user.Address) {
    errors.Address = "Address Required";
  }
  if (user.Phone.length == 0) {
    errors.Phone = "Phone Number Required";
  } else if (user.Phone.length < 11) {
    errors.Phone = "Phone number should be 11 digits long";
  } else if (
    !/^((\+92)|(0092))-{0,1}\d{3}-{0,1}\d{7}$|^\d{11}$|^\d{4}-\d{7}$/.test(
      user.Phone
    )
  ) {
    errors.Phone = "Invalid Phone Number";
  }

  if (!user.email) {
    errors.email = "Email Required";
  } else if (!/^[A-Z0-9._%+=]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(user.email)) {
    errors.email = "Invalid Email Address";
  }
  if (!user.Password) {
    errors.Password = "Password Required";
  } else if (
    !/^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#\$%\^&\*])(?=.{8,})/.test(
      user.Password
    )
  ) {
    errors.Password =
      "Password should contain one capital letter, one small letter, one number, one special character";
  }

  return errors;
}

export { validateInfo };
