import React, { useState } from "react";
import axios from "axios";
import jwt_decode from "jwt-decode";

function ChangePassword(props) {
  const [password, setPassword] = useState("");

  function jwtDecoder() {
    const token = localStorage.getItem("token");
    const decoded = jwt_decode(token);
    console.log(decoded);
    return decoded;
  }

  async function apiCaller(reqBody) {
    try {
      const decodedToken = jwtDecoder();
      const userID = decodedToken.id;
      const apiUrl =
        "https://fitnessappauth.herokuapp.com/api/users/changeoldpassword/" +
        userID;

      const config = {
        headers: {
          "x-auth-token": localStorage.getItem("token"),
        },
      };
      const res = await axios.post(apiUrl, reqBody, config);
      console.log(res);
      return res;
    } catch (e) {
      console.log(e);
    }
  }
  function handleChange(event) {
    setPassword(event.target.value);
  }

  async function handleSubmit(event) {
    try {
      event.preventDefault();
      const reqBody = {
        Password: password,
      };

      const res = await apiCaller(reqBody);
      if (res.status == "200") {
        props.history.push("/");
      }
    } catch (e) {
      console.log(`inside handler ${e}`);
    }
  }
  return (
    <>
      <main>
        <section className="absolute w-full h-full">
          <div
            className="absolute top-0 w-full h-full bg-gray-900"
            style={{
              backgroundImage:
                "url(" +
                require("./../assets/img/register_bg_2.png").default +
                ")",
              backgroundSize: "100%",
              backgroundRepeat: "no-repeat",
            }}
          ></div>
          <div className="container mx-auto px-4 h-full">
            <div className="flex content-center items-center justify-center h-full">
              <div className="w-full lg:w-4/12 px-4">
                <div className="relative flex flex-col min-w-0 break-words w-full mb-6 shadow-lg rounded-lg bg-gray-300 border-0">
                  <div className="rounded-t mb-0 px-6 py-6"></div>
                  <div className="flex-auto px-4 lg:px-10 py-10 pt-0">
                    <div className="text-gray-500 text-center mb-3 font-bold">
                      <small>Change Password</small>
                    </div>
                    <form onSubmit={handleSubmit}>
                      <div className="relative w-full mb-3">
                        <label
                          className="block uppercase text-gray-700 text-xs font-bold mb-2"
                          htmlFor="grid-password"
                        >
                          New Password
                        </label>
                        <input
                          type="password"
                          onChange={handleChange}
                          className="border-0 px-3 py-3 placeholder-gray-400 text-gray-700 bg-white rounded text-sm shadow focus:outline-none focus:ring w-full"
                          placeholder="New Password"
                          style={{ transition: "all .15s ease" }}
                        />
                      </div>

                      <div className="text-center mt-6">
                        <button
                          className="bg-gray-900 text-white active:bg-gray-700 text-sm font-bold uppercase px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 w-full"
                          type="submit"
                          style={{ transition: "all .15s ease" }}
                        >
                          Change Password
                        </button>
                      </div>
                    </form>
                  </div>
                </div>
                <div className="flex flex-wrap mt-6">
                  <div className="w-1/2 text-right">
                    <a
                      href="#pablo"
                      onClick={(e) => e.preventDefault()}
                      className="text-gray-300"
                    >
                      <small>Create new password</small>
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </main>
    </>
  );
}

export { ChangePassword };
